.navbar {
    margin-bottom: 0;
    display: flex;
    justify-content: space-between;
    background-color: rgba(20, 19, 19, 0.8);
    width: 100%;
    position: fixed;
    z-index: 99;
    height: 50px;
    padding-left: 50px;
    padding-right: 50px;

  }
  .clickgamebtn {
    background-color: transparent;
    border-style: none;

  }
  .clickgamebtn:focus {
    outline: none !important;
  }

  button:focus {
    outline: none !important;
  }
  
  .partitle {
      
      color: white;

      font-family: 'Luckiest Guy', cursive;
      letter-spacing: 2px;
      font-size: 20px;
      
  }

  .partitle:hover {
  
    -ms-transform: scale(1.2); /* IE 9 */
       -webkit-transform: scale(1.2); /* Safari 3-8 */
       transform: scale(1.2); 
       transition:all 0.5s ease;
 }

 .par1 {
     color: white;
     font-family: 'Lato', sans-serif;
     font-size: 15px;
 }


 @media (max-width: 650px){

  .navbar {
    height: 75px;
    z-index: 99;
  }

  .par2 {
    display: none;
  }

 }

 @media (device-height: 375px) and (device-width: 812px) and (-webkit-min-device-pixel-ratio: 2){
  .navbar {
    height: 75px;
    z-index: 99;
  }

  .par2 {
    display: none;
  }
  
  }

  @media (device-height: 568px) and (device-width: 320px) and (-webkit-min-device-pixel-ratio: 2){
